import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import styles from "./AllHotels.module.css";

// Типи для готелів
interface Hotel {
  id: number;
  name: string;
  price: number;
  rating: number;
  date: string;
  imageUrl: string;
  discount?: number;  // Додаємо поле для знижки
}

const AllHotels = () => {
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();
  useEffect(() => {
    // Блокуємо прокрутку на тілі, коли відкривається AllHotels
    document.body.style.overflow = 'hidden';

    // Очистка при закритті компонента, відновлюємо прокрутку
    return () => {
      document.body.style.overflow = 'auto';
    };
  }, []);

  const [hotels, setHotels] = useState<Hotel[]>([
    { id: 1, name: "Hotel Paris", price: 200, rating: 4.5, date: "2025-03-20", imageUrl: "/findHotelWeb/image/ImgHotels_1.png" },
    { id: 2, name: "Hotel London", price: 150, rating: 4.0, date: "2025-03-19", imageUrl: "/findHotelWeb/image/ImgHotels_2.png" },
    { id: 3, name: "Hotel New York", price: 300, rating: 5.0, date: "2025-03-18", imageUrl: "/findHotelWeb/image/ImgHotels_3.png", discount: 10 },  // Готель 3 зі знижкою
    { id: 4, name: "Hotel Tokyo", price: 180, rating: 4.2, date: "2025-03-17", imageUrl: "/findHotelWeb/image/ImgHotels_4.png" },
    { id: 5, name: "Hotel Sydney", price: 250, rating: 4.7, date: "2025-03-16", imageUrl: "/findHotelWeb/image/ImgHotels_5.png" },
    { id: 6, name: "Hotel Berlin", price: 220, rating: 4.3, date: "2025-03-15", imageUrl: "/findHotelWeb/image/ImgHotels_6.png" },
    { id: 7, name: "Hotel Rome", price: 280, rating: 4.6, date: "2025-03-14", imageUrl: "/findHotelWeb/image/ImgHotels_7.png", discount: 15 }, // Готель 7 зі знижкою
    { id: 8, name: "Hotel Los Angeles", price: 350, rating: 4.8, date: "2025-03-13", imageUrl: "/findHotelWeb/image/ImgHotels_8.png" },
    { id: 9, name: "Hotel Barcelona", price: 200, rating: 4.0, date: "2025-03-12", imageUrl: "/findHotelWeb/image/ImgHotels_9.png", discount: 20 }, // Готель 9 зі знижкою
    { id: 10, name: "Hotel Miami", price: 270, rating: 4.4, date: "2025-03-11", imageUrl: "/findHotelWeb/image/ImgHotels_10.png" },
    { id: 11, name: "Hotel Zurich", price: 290, rating: 4.9, date: "2025-03-10", imageUrl: "/findHotelWeb/image/ImgHotels_11.png" },
    { id: 12, name: "Hotel Bukovel", price: 370, rating: 5.0, date: "2025-04-10", imageUrl: "/findHotelWeb/image/ImgHotels_12.png" },
  ]);

  const [priceOrder, setPriceOrder] = useState<string>("ascending");
  const [ratingOrder, setRatingOrder] = useState<string>("descending");
  const [searchTerm, setSearchTerm] = useState<string>("");
  const [showDiscounted, setShowDiscounted] = useState<boolean>(false); // Фільтр для акційних готелів

  const [appliedFilters, setAppliedFilters] = useState({
    searchTerm: "",
    priceOrder: "ascending",
    ratingOrder: "descending",
    showDiscounted: false,
  });

  // Функція для застосування фільтрів
  const applyFilters = () => {
    setAppliedFilters({
      searchTerm,
      priceOrder,
      ratingOrder,
      showDiscounted,
    });
  };

  // Фільтрація готелів з урахуванням пошуку, знижок, ціни та рейтингу
  const filteredHotels = hotels
    .filter((hotel) => {
      const matchesSearchTerm = hotel.name.toLowerCase().includes(appliedFilters.searchTerm.toLowerCase());
      const matchesDiscount = appliedFilters.showDiscounted ? hotel.discount !== undefined : true;
      return matchesSearchTerm && matchesDiscount;
    })
    .sort((a, b) => {
      // Спочатку сортуємо за ціною
      if (appliedFilters.priceOrder === "ascending") {
        return a.price - b.price;
      } else if (appliedFilters.priceOrder === "descending") {
        return b.price - a.price;
      }

      // Потім сортуємо за рейтингом, якщо ціни рівні
      if (appliedFilters.ratingOrder === "ascending") {
        return a.rating - b.rating;
      } else if (appliedFilters.ratingOrder === "descending") {
        return b.rating - a.rating;
      }

      return 0; // Якщо сортування не потрібно
    });

  return (
    <section className={styles.HotelsSection}>
      <button className={styles.buttonClose} onClick={() => navigate("/")}>Закрити</button>
      <div className={styles.content}>
        <h1 className={styles.textH1}> {t('AllHotels.text_4')}</h1>

        <div className="filter">
          <input
            type="text"
            placeholder="Пошук за назвою"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        {/* Сортування */}
        <div className="sort-filters">
          <div>
            <label>Сортувати за ціною:</label>
            <select onChange={(e) => setPriceOrder(e.target.value)} value={priceOrder}>
              <option value="ascending">Від найнижчої до найвищої</option>
              <option value="descending">Від найвищої до найнижчої</option>
            </select>
          </div>

          <div>
            <label>Сортувати за рейтингом:</label>
            <select onChange={(e) => setRatingOrder(e.target.value)} value={ratingOrder}>
              <option value="ascending">Від найнижчого до найвищого</option>
              <option value="descending">Від найвищого до найнижчого</option>
            </select>
          </div>

          <div>
            <label>Показати акційні готелі:</label>
            <input
              type="checkbox"
              checked={showDiscounted}
              onChange={() => setShowDiscounted(!showDiscounted)}
            />
          </div>

          {/* Кнопка для застосування фільтрів */}
          <button className={styles.applyFiltersButton} onClick={applyFilters}>
            Застосувати фільтри
          </button>
        </div>

        {/* Відображення готелів */}
        <div className={styles.hotel_list}>
          {filteredHotels.map((hotel) => (
            <div key={hotel.id} className="hotel-card">
              <img src={hotel.imageUrl} alt={hotel.name} />
              <h3>{hotel.name}</h3>
              <p>Ціна: ${hotel.price}</p>
              <p>Рейтинг: {hotel.rating}</p>
              <p>Дата: {hotel.date}</p>
              {hotel.discount && <p>Знижка: {hotel.discount}%</p>}
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default AllHotels;
